import ava from '../../img/ava.jpg'

function NewPost(props) {
   console.log(props.profile);
    return (
        <div key={props.id} className='new-post'>
            <div className='new-post__img'>
                {props.profile && props.profile.photos.small
                    ? <img className='psotPhoto' src={props.profile.photos.small} />
                    : <img className='psotPhoto' src={ava} alt="" />
                }
            </div>
            <div className="new-post__text">{props.name}{props.message}</div>
        </div>
    );
}

export default NewPost